[type='range'] {
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;
  border-radius: 5px;
  background: transparent; }
  [type='range']::-moz-focus-outer {
    border: 0; }
  [type='range']:focus {
    outline: 0; }
    [type='range']:focus::-webkit-slider-runnable-track {
      background: #848484; }
    [type='range']:focus::-ms-fill-lower {
      background: #777777; }
    [type='range']:focus::-ms-fill-upper {
      background: #848484; }
  [type='range']::-webkit-slider-runnable-track {
    cursor: pointer;
    height: 8px;
    transition: all 0.2s ease;
    width: 100%;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px rgba(13, 13, 13, 0.2);
    background: #777777;
    border: 0px solid #cfd8dc;
    border-radius: 5px; }
  [type='range']::-webkit-slider-thumb {
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2), 0 0 4px rgba(13, 13, 13, 0.2);
    background: #FFF;
    border: 0px solid #eceff1;
    border-radius: 10px;
    cursor: pointer;
    height: 20px;
    width: 20px;
    -webkit-appearance: none;
    margin-top: -6px; }
  [type='range']::-moz-range-track {
    cursor: pointer;
    height: 8px;
    transition: all 0.2s ease;
    width: 100%;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px rgba(13, 13, 13, 0.2);
    background: #777777;
    border: 0px solid #cfd8dc;
    border-radius: 5px; }
  [type='range']::-moz-range-thumb {
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2), 0 0 4px rgba(13, 13, 13, 0.2);
    background: #FFF;
    border: 0px solid #eceff1;
    border-radius: 10px;
    cursor: pointer;
    height: 20px;
    width: 20px; }
  [type='range']::-ms-track {
    cursor: pointer;
    height: 8px;
    transition: all 0.2s ease;
    width: 100%;
    background: transparent;
    border-color: transparent;
    border-width: 10px 0;
    color: transparent; }
  [type='range']::-ms-fill-lower {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px rgba(13, 13, 13, 0.2);
    background: #6a6a6a;
    border: 0px solid #cfd8dc;
    border-radius: 10px; }
  [type='range']::-ms-fill-upper {
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2), 0 0 1px rgba(13, 13, 13, 0.2);
    background: #777777;
    border: 0px solid #cfd8dc;
    border-radius: 10px; }
  [type='range']::-ms-thumb {
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2), 0 0 4px rgba(13, 13, 13, 0.2);
    background: #FFF;
    border: 0px solid #eceff1;
    border-radius: 10px;
    cursor: pointer;
    height: 20px;
    width: 20px;
    margin-top: 0; }

body.hasHover .drum-pad:hover {
  background-color: #5e5e5e; }

.drum-machine-wrapper {
  background-color: #777777; }

.drum-machine {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 90vw;
  max-width: 50.4rem;
  margin: auto;
  font-size: 1.5rem;
  text-align: center;
  background-color: #AAA;
  box-shadow: 0 0 0.5rem #000;
  border-radius: 0.5rem;
  user-select: none;
  -webkit-tap-highlight-color: transparent; }
  .drum-machine .drum-pad-container {
    width: 30.4rem;
    height: 30.4rem;
    padding: 1.6rem;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-content: space-between;
    align-items: center;
    flex-flow: wrap; }
    .drum-machine .drum-pad-container .drum-pad {
      font-size: 2rem;
      font-weight: bold;
      width: 8rem;
      height: 8rem;
      border-radius: 1rem;
      box-shadow: 0.3rem 0.3rem 0.5rem #333333;
      background-color: #777777;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer; }
      .drum-machine .drum-pad-container .drum-pad.active {
        box-shadow: 0 0 0.5rem #333333;
        background-color: #4CD964;
        transform: translate(0.3rem, 0.3rem); }
  .drum-machine .drum-pad-control {
    width: 20rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .drum-machine .drum-pad-control .volume {
      width: 15rem; }
    .drum-machine .drum-pad-control .drum-pad-display {
      width: 12rem;
      margin-bottom: 0.5rem;
      font-weight: bold;
      line-height: 2.5;
      background-color: #4CD964; }

@media (max-width: 600px) {
  .drum-machine {
    width: 30.4rem; } }
